import React from 'react';
import {
  Box,
  Accordion, AccordionSummary, AccordionDetails,
  TableContainer, Table, TableRow, TableCell, TableBody,
  Paper, Typography,
} from '@mui/material';
import {
  ExpandMore as ExpandMoreIcon,
  CatchingPokemon as CatchingPokemonIcon,
  CalendarMonth as CalendarMonthIcon,
  Attractions as AttractionsIcon,
  SportsMma as SportsMmaIcon,
  FormatListNumbered as FormatListNumberedIcon,
} from '@mui/icons-material';

import { rareDataList, gachaTypeList, truncateDecimal } from '../context/Config';
import { useStateContext } from '../context/StateContext';



const Regulation: React.FC = () => {
  const {
    settingsList,
  } = useStateContext();
  const raresLength: number = Object.keys(settingsList.rares).length;



  return (
    <Box sx={{ mt: 2 }}>
      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <CatchingPokemonIcon />
          <Typography sx={{ ml: 2 }}>
            基本
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                    図鑑範囲
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.dexRange}匹
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    パーティのポケモン数
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.partyTotalNum}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    色違いの確率
                  </TableCell>
                  <TableCell align='center'>
                    {`${truncateDecimal(settingsList.shinyProbability * 100)}%`}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    初期経験値
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.initialPokeExp}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    最小経験値
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.minExp}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    最大経験値
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.maxExp}
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>

      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <CalendarMonthIcon />
          <Typography sx={{ ml: 2 }}>
            デイリー
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                    ガチャ回数
                  </TableCell>
                  <TableCell align='center'>
                    {`+(${settingsList.initialGachaCount - 1} + 連続プレイ日数)`}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    BPリセット
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.initialBattlePoint}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    ポケモンの経験値
                  </TableCell>
                  <TableCell align='center'>
                    {`+${settingsList.addExp}`}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    ランキング1位ボーナスのガチャ回数
                  </TableCell>
                  <TableCell align='center'>
                    {`+${settingsList.bonusGachaCount}`}
                  </TableCell>
                </TableRow>

              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>

      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <AttractionsIcon />
          <Typography sx={{ ml: 2 }}>
            ガチャ
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                  </TableCell>
                  {gachaTypeList.map((gacha) => (
                    <TableCell align='center'
                      key={gacha.id}
                    >
                      <img
                        src={gacha.url}
                        alt={gacha.id}
                      />
                    </TableCell>
                  ))}
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    消費ガチャ回数
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.normalGachaCost}
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.premierGachaCost}
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.luxuryGachaCost}
                  </TableCell>
                </TableRow>

                {Array.from({ length: raresLength }, (_, index) => (
                  <TableRow key={index + 1} sx={{ bgcolor: rareDataList[index + 1].color[100] }}>
                    <TableCell align='center'>
                      {`レア度${index + 1}`}
                    </TableCell>
                    <TableCell align='center'>
                      {`${truncateDecimal(settingsList.rares[String(index + 1)].probability * 100)}%`}
                    </TableCell>
                    <TableCell align='center'>
                      {`${truncateDecimal(settingsList.rares[String(index + 1)].probabilityPremier * 100)}%`}
                    </TableCell>
                    <TableCell align='center'>
                      {`${truncateDecimal(settingsList.rares[String(index + 1)].probabilityLuxury * 100)}%`}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>

      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <SportsMmaIcon />
          <Typography sx={{ ml: 2 }}>
            バトル
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper} sx={{ mt: 2 }}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                    消費BP
                  </TableCell>
                  <TableCell align='center'>
                    {'(レア度 + 1) x 経験値'}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    野生のレア度
                  </TableCell>
                  <TableCell align='center'>
                    ノーマルガチャと同じ
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    野生の経験値
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.initialPokeExp}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    勝敗
                  </TableCell>
                  <TableCell align='center'>
                    野生のポケモンよりレア度が高く、経験値が高い方が勝率が高い。
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    勝ったときのガチャ回数
                  </TableCell>
                  <TableCell align='center'>
                    {`+${settingsList.winGachaCount}`}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    勝ったときに得る経験値が増える条件
                  </TableCell>
                  <TableCell align='center'>
                    野生のポケモンよりレア度が低い。経験値が低い。
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    負けたときに失う経験値が増える条件
                  </TableCell>
                  <TableCell align='center'>
                    野生のポケモンよりレア度が高い。経験値が高い。レア度が高い。最大経験値。
                  </TableCell>
                </TableRow>

              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>

      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <FormatListNumberedIcon />
          <Typography sx={{ ml: 2 }}>
            ランキング
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper} sx={{ mt: 2 }}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                    スコア
                  </TableCell>
                  <TableCell align='center'>
                    {'(レア度 + 1) x 経験値'}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    色違いのスコア倍率
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.shinyPoint}
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>
    </Box>
  )
}

export default Regulation;
